import { useState, useEffect, useCallback, useMemo } from 'react';
import { CallEnd, VolumeUp, GraphicEq, FullscreenExit } from '@mui/icons-material';
import { Peer } from "peerjs";
import './style.scss'

function VideoCall() {
  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const peerId = urlParams.get('peer');
  let data;
  if( peerId === '1' ) {
    data = {
      localId : '9b579c9a-aefe-4a5b-a842-3681b24ffdee',
      remoteId : 'a9880e9c-9ef9-4eec-bc29-6edabff71de3'
    };
  } else {
    data = {
      localId : 'a9880e9c-9ef9-4eec-bc29-6edabff71de3',
      remoteId : '9b579c9a-aefe-4a5b-a842-3681b24ffdee'
    };
  }

  const [localVideo, setLocalVideo] = useState(null);
  const [remoteVideo, setRemoteVideo] = useState(null);
  const [call, setCall] = useState(null);
  const [connection, setConnection] = useState(null);
  const [specialState, setSpecialState] = useState('');

  const peer = useMemo(() => new Peer(data.localId), [data.localId]);

  useEffect(() => {
    const getLocalVideo = async () => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia(
          {video: true, audio: true}
        );
        setLocalVideo(stream);
      } catch {
        setSpecialState('Please allow access to your camera and microphone');
      }
    };
    getLocalVideo();
  }, []);

  useEffect(() => {
    peer.on('call', (call) => {
      setCall(call);
      peer.on('connection', function(connect) {
        setConnection(connect);
      });
    })
  }, [peer, localVideo]);

  const handleCall = useCallback(() => {
    setSpecialState('Calling...');
    const peerCall = peer.call(data.remoteId, localVideo);
    setCall(peerCall);
    const connect = peer.connect(data.remoteId);
    setConnection(connect)
    peerCall.on('stream', function(remoteStream) {
      setRemoteVideo(remoteStream);
    });
  }, [peer, localVideo, data]);

  const handleAnswer = useCallback(() => {
    call.answer(localVideo);
    call.on('stream', (remoteStream) => {
      setRemoteVideo(remoteStream);
    });
  }, [call, localVideo]);

  const handleExit = useCallback(() => {
    if (connection) {
      connection.close();
    }
  }, [connection]);

  useEffect(() => {
    if (connection && call) {
      connection.on('close', function() {
        call.close();
        setRemoteVideo(null);
        setSpecialState('The call is ended');
      });
    }
  }, [connection, call]);

  return (
    <div className='position-relative'>
      <video
        id='remoteVideo'
        className='bg-secondary w-100 rounded'
        autoPlay
        playsInline
        ref={video => {
          if (video && remoteVideo) {
            video.srcObject = remoteVideo;
          }
        }}
      ></video>
      <div className='position-absolute bottom-0 end-0 mb-2 me-1'>
        <video
          id='localVideo'
          className='bg-secondary-subtle rounded shadow-sm'
          muted
          autoPlay
          playsInline
          ref={video => {
            if (video && localVideo) {
              video.srcObject = localVideo;
            }
          }}
        />
        <div className='callOptions rounded p-2 mt-3 d-flex justify-content-between shadow-sm'>
          <div className='d-flex align-items-center'>
            <VolumeUp className='me-3 fs-2 p-2 rounded-circle bg-primary text-white cursor-pointer' />
            <GraphicEq className='me-3 fs-2 p-2 rounded-circle bg-primary text-white cursor-pointer' />
            <FullscreenExit className='me-3 fs-2 p-2 rounded-circle bg-primary text-white cursor-pointer' />
          </div>
          <CallEnd
            className='fs-1 p-2 rounded-circle bg-danger text-white cursor-pointer'
            onClick={handleExit}
          />
        </div>
      </div>
      <div className='position-absolute top-50 start-50 translate-middle'>
        {!remoteVideo && 
          (specialState !== '' ? (
            <span className='text-white'>{specialState}</span>
          ) : (
            <button
              className='btn btn-primary'
              onClick={call ? handleAnswer : handleCall}
            >{call ? `Click To Answer` : `Click To Call`}</button>
          ))
        }
      </div>
    </div>
  );
}

export default VideoCall;